"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { useAuth } from "@/contexts/AuthContext"
import Link from "next/link"
import { getSubjects, getChapters, getQuizzes, createQuiz, deleteQuiz, getSemester } from "@/lib/firestore"
import type { Subject, Chapter, Quiz, Semester } from "@/types"

export default function QuizzesPage({ params }: { params: { semesterId: string } }) {
  const { user } = useAuth()
  const [subjects, setSubjects] = useState<Subject[]>([])
  const [chapters, setChapters] = useState<Chapter[]>([])
  const [quizzes, setQuizzes] = useState<Quiz[]>([])
  const [semester, setSemester] = useState<Semester | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")

  // Form state
  const [showForm, setShowForm] = useState(false)
  const [selectedSubject, setSelectedSubject] = useState("")
  const [formData, setFormData] = useState({
    chapterId: "",
    question: "",
    options: ["", "", "", ""],
    correctAnswer: 0,
    points: 10
  })

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [semesterData, subjectsData] = await Promise.all([
          getSemester(params.semesterId),
          getSubjects(params.semesterId)
        ])
        setSemester(semesterData)
        setSubjects(subjectsData)

        // Fetch chapters for all subjects
        const allChapters = await Promise.all(
          subjectsData.map(subject => getChapters(subject.id))
        )
        const flattenedChapters = allChapters.flat()
        setChapters(flattenedChapters)

        // Fetch quizzes for all chapters
        const allQuizzes = await Promise.all(
          flattenedChapters.map(chapter => getQuizzes(chapter.id))
        )
        setQuizzes(allQuizzes.flat())
      } catch (error) {
        console.error("Error fetching data:", error)
        setError("Failed to load data")
      } finally {
        setLoading(false)
      }
    }

    fetchData()
  }, [params.semesterId])

  // Get chapters for selected subject
  const subjectChapters = chapters.filter(ch => ch.subjectId === selectedSubject)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    try {
      await createQuiz(formData)
      
      // Refresh quizzes for the selected chapter
      const updatedQuizzes = await getQuizzes(formData.chapterId)
      setQuizzes(prev => [
        ...prev.filter(q => q.chapterId !== formData.chapterId),
        ...updatedQuizzes
      ])
      
      setShowForm(false)
      setFormData({
        chapterId: "",
        question: "",
        options: ["", "", "", ""],
        correctAnswer: 0,
        points: 10
      })
      setSelectedSubject("")
    } catch (error) {
      console.error("Error creating quiz:", error)
      setError("Failed to create quiz")
    } finally {
      setLoading(false)
    }
  }

  const handleDelete = async (quizId: string, chapterId: string) => {
    if (!confirm("Are you sure you want to delete this quiz?")) {
      return
    }
    setLoading(true)
    try {
      await deleteQuiz(quizId)
      const updatedQuizzes = await getQuizzes(chapterId)
      setQuizzes(prev => [
        ...prev.filter(q => q.chapterId !== chapterId),
        ...updatedQuizzes
      ])
    } catch (error) {
      console.error("Error deleting quiz:", error)
      setError("Failed to delete quiz")
    } finally {
      setLoading(false)
    }
  }

  // Only allow admin access
  if (user?.email !== 'jeniperwalk@gmail.com') {
    return (
      <main className="min-h-screen flex items-center justify-center">
        <Card className="p-6 text-center">
          <h1 className="text-2xl font-bold text-gray-800 mb-4">Access Denied</h1>
          <p className="text-gray-600 mb-4">You do not have permission to access this page.</p>
          <Link href="/">
            <Button className="bg-orange-500">Back to Dashboard</Button>
          </Link>
        </Card>
      </main>
    )
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <p>Loading...</p>
      </div>
    )
  }

  return (
    <main className="min-h-screen bg-gradient-to-br from-orange-50/40 to-white p-8">
      <div className="max-w-4xl mx-auto">
        <div className="flex justify-between items-center mb-8">
          <div>
            <Link 
              href="/admin"
              className="text-orange-500 hover:text-orange-600 mb-2 inline-flex items-center gap-2"
            >
              ‚Üê Back to Admin
            </Link>
            <h1 className="text-2xl font-bold text-gray-800">
              Manage Quizzes - Semester {semester?.number}
            </h1>
          </div>
          <Button 
            onClick={() => setShowForm(true)}
            className="bg-green-600 hover:bg-green-700"
            disabled={loading}
          >
            Add Quiz
          </Button>
        </div>

        {error && (
          <div className="bg-red-50 text-red-500 p-4 rounded-lg mb-6">
            {error}
          </div>
        )}

        {showForm && (
          <Card className="p-6 mb-8">
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Subject
                </label>
                <select
                  value={selectedSubject}
                  onChange={(e) => {
                    setSelectedSubject(e.target.value)
                    setFormData(prev => ({ ...prev, chapterId: "" }))
                  }}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                  required
                >
                  <option value="">Select a subject</option>
                  {subjects.map(subject => (
                    <option key={subject.id} value={subject.id}>
                      {subject.name} ({subject.code})
                    </option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Chapter
                </label>
                <select
                  value={formData.chapterId}
                  onChange={(e) => setFormData({ ...formData, chapterId: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                  required
                  disabled={!selectedSubject}
                >
                  <option value="">Select a chapter</option>
                  {subjectChapters.map(chapter => (
                    <option key={chapter.id} value={chapter.id}>
                      {chapter.title}
                    </option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Question
                </label>
                <textarea
                  value={formData.question}
                  onChange={(e) => setFormData({ ...formData, question: e.target.value })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                  rows={3}
                  required
                />
              </div>
              {formData.options.map((option, index) => (
                <div key={index}>
                  <label className="block text-sm font-medium text-gray-700 mb-1">
                    Option {index + 1}
                  </label>
                  <input
                    type="text"
                    value={option}
                    onChange={(e) => {
                      const newOptions = [...formData.options]
                      newOptions[index] = e.target.value
                      setFormData({ ...formData, options: newOptions })
                    }}
                    className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                    required
                  />
                </div>
              ))}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Correct Answer (0-3)
                </label>
                <input
                  type="number"
                  value={formData.correctAnswer}
                  onChange={(e) => setFormData({ ...formData, correctAnswer: parseInt(e.target.value) })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                  min="0"
                  max="3"
                  required
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Points
                </label>
                <input
                  type="number"
                  value={formData.points}
                  onChange={(e) => setFormData({ ...formData, points: parseInt(e.target.value) })}
                  className="w-full px-4 py-2 border border-gray-200 rounded-lg"
                  min="1"
                  required
                />
              </div>
              <div className="flex gap-4">
                <Button 
                  type="submit"
                  className="flex-1 bg-orange-500"
                  disabled={loading}
                >
                  Create Quiz
                </Button>
                <Button 
                  type="button"
                  onClick={() => {
                    setShowForm(false)
                    setSelectedSubject("")
                    setFormData({
                      chapterId: "",
                      question: "",
                      options: ["", "", "", ""],
                      correctAnswer: 0,
                      points: 10
                    })
                  }}
                  className="bg-gray-200 text-gray-700 hover:bg-gray-300"
                  disabled={loading}
                >
                  Cancel
                </Button>
              </div>
            </form>
          </Card>
        )}

        <div className="space-y-8">
          {subjects.map((subject) => {
            const subjectChapters = chapters.filter(ch => ch.subjectId === subject.id)
            if (subjectChapters.length === 0) return null

            return (
              <div key={subject.id}>
                <h2 className="text-xl font-semibold text-gray-800 mb-4">
                  {subject.name} ({subject.code})
                </h2>
                {subjectChapters.map(chapter => {
                  const chapterQuizzes = quizzes.filter(q => q.chapterId === chapter.id)
                  if (chapterQuizzes.length === 0) return null

                  return (
                    <div key={chapter.id} className="mb-6">
                      <h3 className="text-lg font-medium text-gray-700 mb-3">
                        {chapter.title}
                      </h3>
                      <div className="grid grid-cols-1 gap-4">
                        {chapterQuizzes.map((quiz) => (
                          <Card key={quiz.id} className="p-6">
                            <div className="flex justify-between items-start mb-4">
                              <div className="space-y-2">
                                <h4 className="font-medium text-gray-800">
                                  {quiz.question}
                                </h4>
                                <div className="space-y-1">
                                  {quiz.options.map((option, index) => (
                                    <p 
                                      key={index}
                                      className={index === quiz.correctAnswer ? "text-green-600" : "text-gray-600"}
                                    >
                                      {index + 1}. {option}
                                    </p>
                                  ))}
                                </div>
                                <p className="text-gray-600">Points: {quiz.points}</p>
                              </div>
                              <Button
                                onClick={() => handleDelete(quiz.id, chapter.id)}
                                variant="destructive"
                                size="sm"
                                className="bg-red-500 hover:bg-red-600"
                              >
                                Delete
                              </Button>
                            </div>
                          </Card>
                        ))}
                      </div>
                    </div>
                  )
                })}
              </div>
            )
          })}
        </div>
      </div>
    </main>
  )
}
